body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
body {
    background-color: #282c34;
    font-family: 'Lato', sans-serif;
}

.App {
    text-align: center;
}

.page-wrap {
    display: flex;
    flex-direction: column;
    color: white;
}

/* LANDING PAGE | HERO SECTION */

.wrapper {
    height: 300vh;
}

#image-container {
    background-image: linear-gradient(rgba(0, 0, 0, 0.45), rgba(0, 0, 0, 0.45)), url(/brendanjmyers/static/media/hero_bg2.86e6e797.jpeg);
    background-position: 50% 70%;
    background-size: cover;
    background-attachment: fixed;
    width: 100%;
    height: calc(100vh - 50px);
}

.hero-text-container {
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    text-align: center;
}

.hero-header {
    margin-top: 275px !important;
    font-size: 50px;
    text-transform: uppercase;
    margin-bottom: 10px !important;
    font-weight: 400;
}

.primary {
    color: #0081f7;
    font-weight: 700;
}

.primary-tagline {
    line-height: 1em;
    -webkit-font-feature-settings: "kern";
            font-feature-settings: "kern";
    -webkit-font-kerning: normal;
            font-kerning: normal;
    text-align: center;
    font-weight: 600;
    color: #0081f7;
    font-size: 30px;
    text-transform: uppercase;
}

.secondary-tagline {
    line-height: 1em;
    text-align: center;
    font-weight: 300;
    color: white;
    font-size: 23px;
    text-transform: uppercase;
}

.secondary-tagline::after {
    display: block;
    content: ' ';
    height: 3px;
    background-color: #0081f7;
    text-align: center;
    width: 6em;
    margin: 8px auto 20px;
}

.short-desc {
    font-size: 21px;
    text-align: center;
    line-height: 1em;
    color: #aeaeae;
    padding: 3px;
}

/* ABOUT ME SECTION */

.self-photo {
    margin: 50px;
    width: 33%;
    height: auto;
    display: inline-block !important;
    float: left;
}

.about.segment {
    font-size: 20px;
    line-height: 1em;
    padding: 50px 100px 50px 0;
}

.about-header {
    font-size: 48px;
    text-transform: uppercase;
    line-height: 1em;
}

.about-header::after {
    display: block;
    content: ' ';
    height: 3px;
    width: 2em;
    margin: 50px 0;
    background-color: white;
}

#sticky-menu {
    height: 50px;
    background-color: #222222;
    border-radius: 0;
}

.navbar-placeholder {
    height: 50px;
}

#sticky-menu .item {
    color: #9b9b9b;
    font-size: 16px;
    text-transform: uppercase;
}

.menu-header {
    font-size: 24px;
    margin-top: -3px;
}

#sticky-menu .hamburger-btn {
    display: none;
}

@media screen and (max-width: 670px) {
    #sticky-menu .nav-item {
        display: none;
    }

    #sticky-menu .hamburger-btn {
        display: block;
    }
}
/* EXPERIENCE SECTION */

.experience {
    display: flex !important;
    width: 100%;
    text-align: center;
}

.tools, .usage {
    width: 50%;
}

.tools .primary {
    font-size: 32px;
    font-weight: 200;
}

.tools .primary:after {
    display: block;
    content: ' ';
    height: 1px;
    background-color: #0081f7;
    text-align: center;
    width: 9em;
    margin: 15px auto 20px;
}

.tools ul {
    list-style: none;
    display: flex;
    padding-left: 0;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.tools ul li {
    padding: 10px;
}

.tools .logo {
    display: block;
    min-width: 100px;
    max-width: 100px;
    height: auto;
}

.logo {
    transition: ease .25s;
    -webkit-transform: scale(1);
            transform: scale(1);
}

.tools li:hover > .logo {
    transition: ease .25s;
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
}

.white-bg {
    background-color: white !important;
    padding: 10px !important;
}

.teal-bg {
    background-color: #43b1b0 !important;
    padding: 10px !important;
}

.usage .primary {
    font-size: 32px;
    font-weight: 200;
}

.usage .primary:after {
    display: block;
    content: ' ';
    height: 1px;
    background-color: #0081f7;
    text-align: center;
    width: 14em;
    margin: 15px auto 20px;
}

.experience-lists {
    text-align: center;
    font-size: 30px;
}

.heart-container {
    margin-bottom: 10px;
}

.experience-table {
    margin: 0 auto !important;
    color: white !important;
    font-size: 30px !important;
}

.experience-table td {
    padding: 2px 50px !important;
}

.lb-heart {
    background: 0 0 !important;
    color: #8cbbff !important;
    text-shadow: 0 -1px 0 #54b9cd, -1px 0 0 #64c8cd, 0 1px 0 #42c4cd, 1px 0 0 #59c2cd !important;
}

.inactive {
    background: 0 0 !important;
    color: rgba(236, 234, 255, 0.33) !important;
    text-shadow: 0 -1px 0 rgba(18, 14, 16, 0.29), -1px 0 0 rgba(18, 14, 16, 0.31), 0 1px 0 rgba(18, 14, 16, 0.31), 1px 0 0 rgba(18, 14, 16, 0.34) !important;
}

@media screen and (max-width: 1000px) {
    .self-photo {
        display: none !important;
    }

    .segment.about {
        padding: 40px;
    }

    .experience {
        display: grid !important;
        width: 100% !important;
        grid-auto-rows: 1fr;
    }
    .tools {
        display: grid;
        width: 100% !important;
    }

    .usage {
        display: grid;
        width: 100% !important;
    }
}

